@import "variables";


.mainbox-content-container {
  padding: 24px; }

.about-title {
  color: $primary;
  font-family: "Helvetica Now Display",sans-serif;
  font-weight: 700;
  font-size: 2rem;
  padding-top: 5px; }

.underline-title {
  position: relative;
  display: inline-block;
  z-index: 0; }

.underline-title:before {
  content: '';
  max-width: 50px;
  position: absolute;
  z-index: -1;
  left: 0;
  right: 0;
  bottom: -25px;
  height: 0;
  border: 3px solid #304CFD;
  border-radius : 10px; }

.about-text {
  padding: 10px;
  p {
    font-family: "Helvetica Now", sans-serif;
    font-weight: 300;
    font-size: 15px;
    word-wrap: break-word; } }

.projects-container {
  border-radius: 20px;
  background-color: #F5F8F9;
  padding: 1rem 1.9rem;
  line-height: 2.2rem; }
.projects-title {
  color: $primary;
  font-family: "Helvetica Now Display",sans-serif;
  font-weight: 700;
  font-size: 2rem;
  padding-top: 5px; }
.project-card {
  height: 200px;
  width: 300px;
  img {
    margin-top: 15px;
    height: auto;
    width: 100%; } }
