
@import "reset";
@import "fonts";
@import "variables";
@import "sidebox";
@import "mainbox";

body {
  background-color: $primary;
  font-family: $primary_font;
  font-size: 15px; }


h1 {
  font-family: "Helvetica Now Display",sans-serif;
  font-weight: 200;
  font-size: 2rem; }
p {
  font-family: $primary_font;
  font-weight: normal;
  font-size: 15px;
  line-height: 1.5; }

a {
  text-decoration: none;
  color: $tertiary; }

.site-container {
  padding: 3rem 3rem; }



.mainbox {
  float: right;
  background-color: white;
  border-radius: 20px;
  margin-left: 30px; }

.sidebox {
  float: left;
  text-align: center;
  background-color: white;
  border-radius: 20px;
  min-width: 260px; }

.box-container {
  display: flex; }
/* RESPONSIVE  SECTIONS */

.section {
  clear: both;
  padding: 0;
  margin: 0; }


/*  COLUMN SETUP  */
.col {
  display: block;
  float: left;
  margin: 1% 0 1% 1.2%; }

.col:first-child {
  margin-left: 0; }

/*  GROUPING  */
.group:before,
.group:after {
  content: "";
  display: table; }
.group:after {
  clear: both; }
.group {
  zoom: 1; }

/*  GRID OF TWELVE  */

.span_12_of_12 {
  width: 100%; }

.span_11_of_12 {
  width: 91.56%; }

.span_10_of_12 {
  width: 83.13%; }

.span_9_of_12 {
  width: 74.7%; }

.span_8_of_12 {
  width: 66.26%; }

.span_7_of_12 {
  width: 57.83%; }

.span_6_of_12 {
  width: 49.4%; }

.span_5_of_12 {
  width: 40.96%; }

.span_4_of_12 {
  width: 32.53%; }

.span_3_of_12 {
  width: 24.1%; }


.span_2_of_12 {
  width: 15.66%; }


.span_1_of_12 {
  width: 7.233%; }


/*  GO FULL WIDTH BELOW 480 PIXELS */
@media only screen and (max-width: 780px) {
  .col {
    margin: 1% 0 1% 0; }
  .span_1_of_12, .span_2_of_12, .span_3_of_12,
  .span_4_of_12, .span_5_of_12, .span_6_of_12,
  .span_7_of_12, .span_8_of_12, .span_9_of_12,
  .span_10_of_12, .span_11_of_12, .span_12_of_12 {
    width: 100%; } }






